import React,{Component} from 'react';
import {Navbar,NavbarBrand,Jumbotron,NavbarToggler,Collapse,NavItem,Nav} from 'reactstrap';
import {NavLink} from 'react-router-dom';
class Header extends Component{
	constructor(props) {
        super(props);
    
        this.toggleNav = this.toggleNav.bind(this);
        this.state = {
          isNavOpen: false
        };
      }

      toggleNav() {
        this.setState({
          isNavOpen: !this.state.isNavOpen
        });
      }
	render(){
		return(
			<>
			<Navbar dark expand="md">
	      		<div className="container">
	      		<NavbarToggler onClick={this.toogleNav}/>
	        	<NavbarBrand className="mr-auto" href="/">

	        		<img src={process.env.PUBLIC_URL+'/assets/images/logo.png'} height="30" width="41"alt="Ristorante Con Fusion"/>

	        	</NavbarBrand>
	        	<Collapse isOpen={this.state.isNavOpen} navbar>
	        	<Nav navbar>
	        	<NavItem>
	        		<NavLink className="nav-link" to="/home">
	        		<span className="fa fa-home fa-g"></span>Home
	        		</NavLink>
	        	</NavItem>
	        	<NavItem>
	        		<NavLink className="nav-link" to="/aboutus">
	        		<span className="fa fa-info fa-g"></span>AboutUs
	        		</NavLink>
	        	</NavItem>
	        	<NavItem>
	        		<NavLink className="nav-link" to="/menu">
	        		<span className="fa fa-list fa-g"></span>Menu
	        		</NavLink>
	        	</NavItem>
	        	<NavItem>
	        		<NavLink className="nav-link" to="/contactus">
	        		<span className="fa fa-address-card fa-g"></span>Contact
	        		</NavLink>
	        	</NavItem>
	        	</Nav>
	        	</Collapse>
	      		</div>
	      </Navbar>
	      <Jumbotron >
	      <div className="container">
	      	<div className="row row-header">
	      		<div className="col-12 col-sm-6">
	      			<h1>Ristorante Con Fusion</h1>
	      			<p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
	      		</div>
	      	</div>
	      	</div>
	      </Jumbotron>

			</>
		);

	}
}

export default Header;
